import React from "react";
import styles from "./content.module.css";

export default () => (
  <div className={styles.wrapper}>
    <div className={styles.serveList}>
      <div>30天无忧退货</div>
      <div>48小时快速退款</div>
      <div>满88元免邮费</div>
    </div>
    <div className={styles.toBeCenter}>
      <img
        alt=""
        className={styles.shoppingIcon}
        src="//yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/noCart-a8fe3f12e5.png"
      />
      <div className={styles.hint}>去添加点什么吧</div>
      <div className={styles.loginIn}>登录</div>
    </div>
  </div>
);



